.navbar {
  text-align: center; }

@media (max-width: 1088px) {
  .navbar-menu.is-active {
    -webkit-animation-name: burger-menu;
            animation-name: burger-menu;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    overflow: hidden;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-transition: all 600ms cubic-bezier(0.23, 1, 0.32, 1);
    -o-transition: all 600ms cubic-bezier(0.23, 1, 0.32, 1);
    transition: all 600ms cubic-bezier(0.23, 1, 0.32, 1); }
  @-webkit-keyframes burger-menu {
    0% {
      height: 0px; }
    100% {
      height: 210px; } }
  @keyframes burger-menu {
    0% {
      height: 0px; }
    100% {
      height: 210px; } }
  .navbar-menu.is-closed {
    display: block;
    padding: 0px;
    overflow: hidden;
    -webkit-animation-name: burger-menu-close;
            animation-name: burger-menu-close;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-transition: all 600ms cubic-bezier(0.23, 1, 0.32, 1);
    -o-transition: all 600ms cubic-bezier(0.23, 1, 0.32, 1);
    transition: all 600ms cubic-bezier(0.23, 1, 0.32, 1); }
  @-webkit-keyframes burger-menu-close {
    0% {
      height: 210px;
      display: block; }
    99% {
      display: block; }
    100% {
      height: 0px;
      display: none; } }
  @keyframes burger-menu-close {
    0% {
      height: 210px;
      display: block; }
    99% {
      display: block; }
    100% {
      height: 0px;
      display: none; } }
  .navbar-menu .navbar-item {
    padding-bottom: 12.5px;
    padding-top: 12.5px;
    font-weight: 600;
    border-bottom: 1px solid #f3f3f3; }
  .navbar-burger span {
    color: #252525;
    height: 1.5px; } }



