html {
  background: #e9e9e9;
  background: -webkit-gradient(linear, left top, right top, from(#f8f8f8), color-stop(#eeeeee), to(#e9e9e9));
  background: -webkit-linear-gradient(left, #f8f8f8, #eeeeee, #e9e9e9);
  background: -o-linear-gradient(left, #f8f8f8, #eeeeee, #e9e9e9);
  background: linear-gradient(to right, #f8f8f8, #eeeeee, #e9e9e9); }

.projects {
  height: 100vh;
  width: 100vw;
  color: #1b1b1b; }
  .projects-grid-item {
    -webkit-transition: cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s;
    -o-transition: cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s;
    transition: cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s; }
    .projects-grid-item .card-content {
      max-height: 190px; }
      @media (min-width: 770px) {
        .projects-grid-item .card-content {
          min-height: 215px; } }
      @media (min-width: 1100px) {
        .projects-grid-item .card-content {
          min-height: 190px; } }
    .projects-grid-item:hover {
      -webkit-transition: cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s;
      -o-transition: cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s;
      transition: cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s;
      -webkit-transform: scale(1.03);
          -ms-transform: scale(1.03);
              transform: scale(1.03);
      z-index: 5; }



